<template>
  <div>
    <h1>FITS</h1>
    <div v-if="this.display.menubar" :id="`${id}Menubar`" class="JS9Menubar" :data-width="`${width}px`"></div>
    <div v-if="this.display.toolbar" :id="`${id}Toolbar`" class="JS9Toolbar" :data-width="`${width}px`"></div>
    <div if="this.display.main" :id="id" class="JS9" :data-width="`${width}px`" :data-height="`${height}px`"></div>
    <div v-if="this.display.colorbar" style="margin-top: 2px;"></div>
    <div v-if="this.display.colorbar" :id="`${id}Colorbar`" class="JS9Colorbar" :data-width="`${width}px`"></div>
  </div>
</template>
<script>
export default {
  props: {
    id: String,
    source: String,
    width: Number,
    height: Number,
    display: Object
  },
  name: 'VFits',
  mounted: function () {
    /* eslint-disable */
    JS9.Preload(this.source, {display: this.id, scale: "sqrt", colormap: "bb", zoom: 'toFit'})
    /* eslint-enable */
  }
}
</script>
